import FormComponent from "../components/FormComponent"
import ContainerComponent from "../components/ContainerComponent"
import TextComponent from "../components/TextComponent"
import TitleComponent from "../components/TitleComponent"
import ButtonComponent from "../components/ButtonComponent"
import ShareButtonComponent from "../components/ShareButtonComponent"
import SubShareButtonComponent from "../components/SubShareButtonComponent"
import ImageComponent from "../components/ImageComponent"
import {Text, HashTags } from "../common/constant"
import { Base64 } from "../common/base64"

import { useRoutingFunction } from "../hooks/userouting"
import { useResult } from "../util/result"

import { React, useEffect } from "react"
import { PostPageStoriesPhotoMediaRequest } from "instagram-graph-api"

import styled from "styled-components"

import ReactGA from 'react-ga'

const { Kakao } = window;

//     
const Result = ()=> {

    useEffect(()=>{
        Kakao.cleanup();
        // js key
        Kakao.init(process.env.REACT_APP_kakao_js_key);
        console.log(Kakao.isInitialized());

        // google analytics
        const pagePath = window.location.pathname + window.location.search + window.location.hash;
        ReactGA.pageview(pagePath);
    },[]);
    
    const result = useResult();

    const hadleClickTumblbug = () => {
        if (window.gtag) {
            window.gtag('event', 'Click', {
                'event_category': 'User',
                'event_label': 'Result Move to Tumblbug'
            });
        }
        window.open(`${Text.tumblbug_url}`)}

    const handleClickKakao = () => {
        if (window.gtag) {
            window.gtag('event', 'Click', {
                'event_category': 'User',
                'event_label': 'Result Kakao Share'
            });
        }
        const urlstring = result + "_square"
        Kakao.Share.sendDefault({
            objectType: 'feed',
            content: {
                title: '<>  TEST',
                description: `${Text.home_subtitle}`,
                imageUrl:
                `${Text[urlstring]}`,
                link: {
                    mobileWebUrl: process.env.REACT_APP_deployment_url,
                },
            },
            buttons: [
                {
                    title: '  ',
                    link: {
                    mobileWebUrl: process.env.REACT_APP_deployment_url,
                    },
                },
                ],
        })
    }

    const handleClickX = () => {
        if (window.gtag) {
            window.gtag('event', 'Click', {
                'event_category': 'User',
                'event_label': 'Result Twitter Share'
            });
        }
        window.open(`https://www.twitter.com/intent/tweet?&url=${process.env.REACT_APP_deployment_url}`)}
    
    // const urlstring = result + "_share"

    const handleClickInstagram = () => {
        if (window.gtag) {
            window.gtag('event', 'Click', {
                'event_category': 'User',
                'event_label': 'Result Instagram Share'
            });
        }
        // const appID = "910958537141333";
        const instagramStoriesUrl = 'instagram-stories://share';
        
        // handleImageDownload(imageUrl, fileName)
        // source_application=${appID}
        window.open(`${instagramStoriesUrl}?&backgroundImage=${Text[`${result}_imageUrl`]}`);
    }

    const navigation = useRoutingFunction("","E")
    const handleClickRestart = () => {
        navigation()
    }

    const handleImageDownload = (imageUrl, fileName) => {
        if (window.gtag) {
            window.gtag('event', 'Download', {
                'event_category': 'User',
                'event_label': 'Result Image Download'
            });
        }

        const a = document.createElement('a');
        a.href = imageUrl;
        a.download = fileName;
        a.click();
    }

    const handleCopyToClipboard = async () => {
        if (window.gtag) {
            window.gtag('event', 'Click', {
                'event_category': 'User',
                'event_label': 'Result Copy to Clipboard'
            });
        }

        try {
          //  URL 
        await navigator.clipboard.writeText(Text.hozakdo_url);
        console.log('URL ');
        } catch (err) {
        return alert('URL  .', err);
        }
    };

    // const handleClickUpload = () => {
    //     const fileInput = document.getElementById('upload');
    //     const file = fileInput.files[0]; //    

    // if (!file) {
    // console.log('No file selected');
    // return;
    // }

    // Kakao.Share.uploadImage({
    // file: fileInput.files,
    // })
    // .then(response => {
    // console.log('Uploaded image URL:', response.infos.original.url);
    // })
    // .catch(error => {
    // console.error('Error uploading image:', error);
    // });
    // }


    return <CenteredContainer>
        <DivComponent>
        <ImageContainer>
            <Image src={`./img/${result}.jpg`} alt={`   ${result} `} />
        </ImageContainer>
        <ImageDownload 
        $txtcolor = {result === 'warm_magpie'? "black" : "white"}
        onClick={() => handleImageDownload(`./img/${result}_share.jpg`,`${result}`)}>
            <DownloadImage src={result === 'warm_magpie'? `./img/download_black.png` : `./img/download_white.png`}/>
            {Text.download}
        </ImageDownload>
        
        <CenteredContainerComponent>
            <TextComponent 
            msg = {Text[`result_text_${result}_header`]}
            fontsize = "1.2em"
            txtwrap = "wrap"
            >
            </TextComponent>
        </CenteredContainerComponent>
        
        <CenteredContainerSubComponent>
            <TitleComponent 
            msg = {Text[`result_text_${result}_subheader`]}
            $txtcolor = "black"
            txtwrap = "wrap"
            marginTop = "0px"
            marginBottom = "10px"
            >
            </TitleComponent>
        </CenteredContainerSubComponent>

        {/*   */}
        <HashTagContainer>
            {HashTags[`${result}`].map( (tag,i) => <HashTag key={i}>{tag}</HashTag>)}
        </HashTagContainer>

        {/*  */}
        <CenteredContainerTextComponent>
            <TextComponent 
            msg = {Text[`result_text_${result}_1`]}
            fontsize = "1.2em"
            fontFamily = "P-R"
            txtwrap = "wrap"
            >
            </TextComponent>
            </CenteredContainerTextComponent>
            <CenteredContainerTextComponent>
            <TextComponent 
            msg = {Text[`result_text_${result}_2`]}
            fontsize = "1.2em"
            fontFamily = "P-R"
            txtwrap = "wrap"
            >
            </TextComponent>
        </CenteredContainerTextComponent>

        {/*  */}
        <CenteredContainerComponent>
            <ShareButtonComponent 
            msg = {Text.result_tumblbug}
            onClick={hadleClickTumblbug}
            widthratio = "75%"
            fontsize = "1.1em"
            >
            </ShareButtonComponent>
        </CenteredContainerComponent>

        <CenteredContainerComponent>
            <SubShareButtonComponent 
            msg = {Text.result_urlcopy}
            onClick={handleCopyToClipboard}
            widthratio = "75%"
            fontsize = "1.1em"
            >
            </SubShareButtonComponent>
        </CenteredContainerComponent>

        <CenteredContainerComponent>
            <SubShareButtonComponent 
            msg = {Text.result_instagram}
            onClick={() => handleClickInstagram()}
            widthratio = "75%"
            fontsize = "1.1em"
            >
            </SubShareButtonComponent>
        </CenteredContainerComponent>
        
        <CenteredContainerComponent>
            <SubShareButtonComponent 
            msg = {Text.result_X}
            onClick={handleClickX}
            widthratio = "75%"
            fontsize = "1.1em"
            >
            </SubShareButtonComponent>
        </CenteredContainerComponent>
        
        <CenteredContainerComponent>
            <SubShareButtonComponent 
            msg = {Text.result_restart}
            onClick={handleClickRestart}
            widthratio = "75%"
            fontsize = "1.1em"
            >
            </SubShareButtonComponent>
        </CenteredContainerComponent>
        {/*   */}
        {/* <CenteredContainerComponent>
            <input type="file" id="upload" />
            <button onClick={handleClickUpload}>Upload</button>
        </CenteredContainerComponent> */}
        {/*   */}
            <CenteredContainerDummy />
        <KakaoContainerComponent>
            <ButtonComponent 
            msg = {Text.result_kakao}
            onClick={handleClickKakao}
            $txtcolor = "black"
            bgcolor = "rgb(255, 235, 0)"
            radius = "0px"
            widthratio = "100%"
            buttonheight = "100%"
            fontsize = "1.0em"
            fontFamily = "P-EBold"
            >
            <KakaoImage src = "./img/kakao_logo.png"/>
            </ButtonComponent>
        </KakaoContainerComponent>
    </DivComponent>
    </CenteredContainer>
}

const CenteredContainer = styled.div`
    display: flex;
    justify-content: center;
    max-width: 600px;
    width: auto;
    margin: auto;
    overflow: auto;
`;

const CenteredContainerComponent = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 600px;
    width: auto;
    margin: 30px 30px 10px 30px;
`;
const CenteredContainerSubComponent = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 600px;
    width: auto;
    margin: 0px 30px 10px 30px;
`;

const CenteredContainerTextComponent = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 600px;
    width: auto;
    margin: 10px 30px;
`;

const CenteredContainerDummy = styled.div`
    justify-content: center;
    align-items: center;
    max-width: 600px;
    width: auto;
    height: 10.0em;
`;

const ImageContainer = styled.div`
    width: 600px;
    height: 450px;
    max-width: 100%;
    overflow: hidden;
`;

const Image = styled.img`
    width: 100%;
    height: 100%; 
    object-fit: cover; 
    object-position: center 30%;
`;

const KakaoImage = styled.img`
    width: 38px;
`;

const DownloadImage = styled.img`
    width: 21px;
`;

const ImageDownload = styled.button`
    display: flex;
    
    position: absolute;
    top: 405px;
    right: 10px;
    padding: 10px 10px;
    background : transparent;
    border-style : none;
    color: ${props => props.$txtcolor || "rgb(255, 255, 255)"};
    font-size: 1.0em;
    font-family: P-Bold;
    white-space: nowrap;

    @media (min-width:601px) {
        right: unset; 
        left: calc(50% + 160px); 
    };
`

const HashTagContainer = styled.div`
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    max-width: 90%;
    margin: 0px 30px 0px;

    @media (max-width:381px) {
        margin: 0px 70px;
        flex-wrap: wrap;
    };
    @media (min-width: 440px) and (max-width: 600px) {
        margin: 0px calc(45px + 50 * (100vw - 440px) / 160);
    }
    @media (min-width: 600px) {
        margin: 0px 120px;
    }
`

const HashTag = styled.div`
    display: flex;
    align-items: center;
    height: 33px;
    font-family: P-Bold;
    font-size: 0.9em;
    border-radius: 100px;
    background: rgb(241, 243, 248);
    padding: 0px 12px;
    margin: 6px 3px;
    white-space: nowrap;
`
const DivComponent = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: auto;
    max-width: 600px;
    background-color: ${props => props.bgcolor || ''};
    width: 100%;
    overflow: auto;
`

const KakaoContainerComponent = styled.div`
    width: 100%;
    position: fixed;
    z-index: 11;
    bottom: -3px;
    left: 50%; //    
    transform: translateX(-50%); //     
    font-size: 15px;
    height: 50px;
    max-width:600px;
`

export default Result